import React, { useEffect, useState } from 'react';
import { cilDelete, cilPen, cilPenAlt, cilPlus, cilTrash } from '@coreui/icons';
import {
  CBadge,
  CButton,
  CCol,
  CFormFloating,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CListGroup,
  CListGroupItem,
  CRow,
  CTooltip,
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import { api, headers } from 'src/views/api';
import axios from 'axios';
import FilterSubModal from './FilterSubModal';

const FilterList = ({
  filters,
  handleDelete,
  setVisible,
  setSelectedFilter,
  getAllFilters,
}) => {
  const [open, setOpen] = useState(false);
  const [filter, setFilter] = useState(null);
  return (
    <div className='col-md-9'>
      <FilterSubModal
        setVisible={setOpen}
        visible={open}
        filter={filter}
        getAllFilters={getAllFilters}
      />
      <div>
        <CListGroup>
          {filters.map((filter) => (
            <CListGroupItem className='d-flex justify-content-between align-items-center'>
              {filter?.name}
              <div>
                <CIcon
                  name={cilTrash}
                  style={{
                    color: 'red',
                    cursor: 'pointer',
                    marginLeft: 16,
                  }}
                  onClick={() => handleDelete(filter._id)}
                />
                <CIcon
                  name={cilPenAlt}
                  style={{ cursor: 'pointer', marginLeft: 16 }}
                  onClick={() => {
                    setSelectedFilter(filter);
                    setVisible(true);
                  }}
                />
                <CBadge color='primary' shape='rounded-pill'>
                  <CTooltip
                    content={filter?.values?.map((el) => el.name)?.join(' - ')}
                  >
                    <span>{filter?.values?.length}</span>
                  </CTooltip>
                </CBadge>
                <CButton
                  color={
                    filter?.children?.values?.length ? 'primary' : 'warning'
                  }
                  className='m-2'
                  onClick={() => {
                    setOpen(true);
                    setFilter(filter);
                  }}
                >
                  {filter?.children?.values?.length
                    ? 'ویرایش زیر فیلتر'
                    : 'افزودن زیر فیلتر'}
                </CButton>
              </div>
            </CListGroupItem>
          ))}
        </CListGroup>
      </div>
    </div>
  );
};

export default FilterList;
