import React, { useEffect } from 'react';
import { cilDelete, cilPen, cilPenAlt, cilPlus, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CBadge,
  CButton,
  CCol,
  CFormFloating,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CListGroup,
  CListGroupItem,
  CRow,
} from '@coreui/react';
import FilterList from './FilterList';
import axios from 'axios';
import { api, headers } from 'src/views/api';
import FilterEditModal from './FilterEditModal';

const FilterForm = () => {
  const [values, setValues] = React.useState([]);
  const [value, setValue] = React.useState('');
  const [name, setName] = React.useState([]);
  const [categories, setCategories] = React.useState([]);
  const [selectedCategory, setSelectedCategory] = React.useState(null);
  const [filters, setFilters] = React.useState([]);
  const [selectedFilter, setSelectedFilter] = React.useState(null);
  const [editModal, setEditModal] = React.useState(false);

  useEffect(async () => {
    getAllData();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const result = await axios.post(
      `${api}/api/filter`,
      {
        name: name,
        category: selectedCategory,
        values,
      },
      { headers }
    );

    if (result.status === 200) {
      const getFilters = await axios.get(`${api}/api/filters`, { headers });
      setFilters(getFilters?.data);
      setName('');
      setValue('');
      setValues([]);
      setSelectedCategory(null);
    }
  };

  const handleDelete = async (id) => {
    const result = await axios.delete(`${api}/api/filter/${id}`, { headers });

    if (result.status === 200) {
      const getFilters = await axios.get(`${api}/api/filters`, { headers });
      setFilters(getFilters?.data);
    }
  };

  const getAllData = async () => {
    const getCategories = await axios.get(`${api}/api/categories`, { headers });
    setCategories(getCategories?.data?.categories);
    const getFilters = await axios.get(`${api}/api/filters`, { headers });
    setFilters(getFilters?.data);
  };

  const handleFilterFilters = async (id) => {
    if (id !== 'فیلتر بر اساس دسته بندی ها') {
      const result = await axios.get(`${api}/api/filter/category/${id}`, {
        headers,
      });
      setFilters(result?.data);
    } else {
      const getFilters = await axios.get(`${api}/api/filters`, { headers });
      setFilters(getFilters?.data);
    }
  };

  return (
    <div className='container p-4'>
      <div className='row'>
        <div className='col-md-12'>
          <div className='row'>
            <FilterEditModal
              visible={editModal}
              setVisible={setEditModal}
              selectedFilter={selectedFilter}
              setSelectedFilter={setSelectedFilter}
              categories={categories}
              getAllData={getAllData}
            />

            <div className='col-md-9 mb-3'>
              <select
                class='form-select'
                aria-label='Default select example'
                onChange={(e) => handleFilterFilters(e.target.value)}
              >
                <option selected value={null}>
                  فیلتر بر اساس دسته بندی ها
                </option>
                {categories.map((category) => (
                  <option value={category._id}>{category.name}</option>
                ))}
              </select>
            </div>
            <FilterList
              filters={filters}
              handleDelete={handleDelete}
              setVisible={setEditModal}
              setSelectedFilter={setSelectedFilter}
              getAllFilters={async () => {
                const getFilters = await axios.get(`${api}/api/filters`, {
                  headers,
                });
                setFilters(getFilters?.data);
              }}
            />
            <div className='col-md-3'>
              <div className='row'>
                <div className='col-md-12 mb-3'>
                  <CFormFloating>
                    <CFormInput
                      type='text'
                      id='name'
                      placeholder='نام فیلتر را وارد کنید'
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                    />
                    <CFormLabel htmlFor='name'>نام فیلتر</CFormLabel>
                  </CFormFloating>
                </div>
                <div className='col-md-12 mb-3'>
                  <CFormFloating>
                    <CFormSelect
                      id='floatingSelectGrid'
                      aria-label='Floating label select example'
                      onChange={(e) => setSelectedCategory(e.target.value)}
                    >
                      <option>انتخاب کنید</option>
                      {categories.map((category) => (
                        <option value={category._id}>{category.name}</option>
                      ))}
                    </CFormSelect>
                    <CFormLabel htmlFor='floatingSelectGrid'>
                      دسته بندی مورد نظر
                    </CFormLabel>
                  </CFormFloating>
                </div>
                <div className='col-md-12 mb-3'>
                  <div className='row align-items-center'>
                    <div className='col-md-10 mb-3'>
                      <CFormFloating>
                        <CFormInput
                          type='text'
                          id='value'
                          value={value}
                          onChange={(e) => setValue(e.target.value)}
                          placeholder='مقادیر فیلتر را وارد و بروی Enter بزنید'
                        />
                        <CFormLabel htmlFor='name'>مقادیر</CFormLabel>
                      </CFormFloating>
                    </div>
                    <div className='col-md-2'>
                      <CButton
                        shape='rounded-pill'
                        color='primary'
                        onClick={() => {
                          setValues([...values, { name: value }]);
                          setValue('');
                        }}
                      >
                        <CIcon name={cilPlus} />
                      </CButton>
                    </div>
                    <div className='col-md-12'>
                      {values?.map((el) => {
                        return (
                          <CButton
                            color='warning'
                            shape='rounded-pill'
                            onClick={() => {
                              const arr = [...values];
                              const newArr = values.filter(
                                (e) => e.name !== el.name
                              );
                              setValues(newArr);
                            }}
                          >
                            {el?.name}
                          </CButton>
                        );
                      })}
                    </div>
                  </div>
                </div>
                <div className='col-md-12'>
                  <button
                    type='button'
                    class='btn btn-secondary btn-lg btn-block'
                    onClick={handleSubmit}
                  >
                    افزودن فیلتر
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FilterForm;
