import { cilPlus } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CFormFloating,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CFormInput,
  CFormLabel,
  CFormSelect,
} from '@coreui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { api, headers } from 'src/views/api';

const FilterSubModal = ({ visible, setVisible, filter, getAllFilters }) => {
  const [value, setValue] = React.useState('');
  const [children, setChildren] = useState({
    title: '',
    values: [],
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    const filterData = {
      ...filter,
      values: filter?.values?.map((el) => {
        return { name: el?.name };
      }),
      children,
    };
    delete filterData?.createdAt;
    delete filterData?.updatedAt;
    delete filterData?.__v;
    delete filterData?._id;

    const res = await axios.put(
      `${api}/api/filter/${filter?._id}`,
      filterData,
      { headers: headers }
    );

    if (res?.status === 200) {
      toast.success('با موفقیت تغییرات انجام شد');
      setVisible(false);
      getAllFilters();
    }
  };

  useEffect(async () => {
    const res = await axios.get(`${api}/api/filter/${filter?._id}`, {
      headers: headers,
    });

    if (res?.data?.children?.values.length) {
      setChildren(res?.data?.children);
    } else {
      setChildren({
        title: '',
        values: [],
      });
    }
  }, [filter]);

  return (
    <CModal visible={visible} onClose={() => setVisible(false)}>
      <CModalHeader onClose={() => setVisible(false)}>
        <CModalTitle>ویرایش فیلتر</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <div className='row'>
          <div className='col-md-12'>
            <div className='row'>
              <div className='col-md-12 mb-3'>
                <CFormFloating>
                  <CFormInput
                    type='text'
                    id='name'
                    placeholder='نام فیلتر را وارد کنید'
                    value={children.title}
                    onChange={(e) =>
                      setChildren({
                        ...children,
                        title: e.target.value,
                      })
                    }
                  />
                  <CFormLabel htmlFor='name'>عنوان</CFormLabel>
                </CFormFloating>
              </div>
              <div className='col-md-12 mb-3'>
                <div className='row align-items-center'>
                  <div className='col-md-10 mb-3'>
                    <CFormFloating>
                      <CFormInput
                        type='text'
                        id='value'
                        value={value}
                        onChange={(e) => setValue(e.target.value)}
                        placeholder='مقادیر فیلتر را وارد و بروی Enter بزنید'
                      />
                      <CFormLabel htmlFor='name'>مقادیر</CFormLabel>
                    </CFormFloating>
                  </div>
                  <div className='col-md-2'>
                    <CButton
                      shape='rounded-pill'
                      color='primary'
                      onClick={() => {
                        setChildren({
                          ...children,
                          values: [...children.values, value],
                        });
                        setValue('');
                      }}
                    >
                      <CIcon name={cilPlus} />
                    </CButton>
                  </div>
                  <div className='col-md-12'>
                    {children?.values?.map((el) => {
                      return (
                        <CButton
                          color='warning'
                          shape='rounded-pill'
                          onClick={() => {
                            const newArr = children.values.filter(
                              (e) => e !== el
                            );
                            setChildren({
                              ...children,
                              values: newArr,
                            });
                          }}
                        >
                          {el}
                        </CButton>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </CModalBody>
      <CModalFooter>
        <CButton color='secondary' onClick={() => setVisible(false)}>
          انصراف
        </CButton>
        <CButton color='primary' onClick={handleSubmit}>
          ذخیره تغییرات
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default FilterSubModal;
