import { cilPlus } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CFormFloating,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CFormInput,
  CFormLabel,
  CFormSelect,
} from '@coreui/react';
import axios from 'axios';
import React, { useEffect } from 'react';
import { toast } from 'react-toastify';
import { api } from 'src/views/api';

const FilterEditModal = ({
  visible,
  setVisible,
  selectedFilter,
  setSelectedFilter,
  categories,
  getAllData,
}) => {
  const [values, setValues] = React.useState([]);
  const [value, setValue] = React.useState('');
  const [name, setName] = React.useState();
  const [selectedCategory, setSelectedCategory] = React.useState(null);

  useEffect(() => {
    setValues(selectedFilter?.values);
    setName(selectedFilter?.name);
    setSelectedCategory(
      categories?.find((el) => el._id === selectedFilter?.category)?._id
    );
  }, [selectedFilter]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const result = await axios.put(`${api}/api/filter/${selectedFilter?._id}`, {
      name,
      category: selectedCategory,
      values,
    });

    if (result.status === 200) {
      await getAllData();
      toast.success('فیلتر با موفقیت ویرایش شد.');
      setSelectedFilter(null);
      setVisible(false);
    }
  };
  return (
    <CModal visible={visible} onClose={() => setVisible(false)}>
      <CModalHeader onClose={() => setVisible(false)}>
        <CModalTitle>ویرایش فیلتر</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <div className='row'>
          <div className='col-md-12'>
            <div className='row'>
              <div className='col-md-12 mb-3'>
                <CFormFloating>
                  <CFormInput
                    type='text'
                    id='name'
                    placeholder='نام فیلتر را وارد کنید'
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                  <CFormLabel htmlFor='name'>نام فیلتر</CFormLabel>
                </CFormFloating>
              </div>
              <div className='col-md-12 mb-3'>
                <CFormFloating>
                  <CFormSelect
                    id='floatingSelectGrid'
                    aria-label='Floating label select example'
                    onChange={(e) => setSelectedCategory(e.target.value)}
                    value={selectedCategory}
                  >
                    <option>انتخاب کنید</option>
                    {categories.map((category) => (
                      <option value={category._id}>{category.name}</option>
                    ))}
                  </CFormSelect>
                  <CFormLabel htmlFor='floatingSelectGrid'>
                    دسته بندی مورد نظر
                  </CFormLabel>
                </CFormFloating>
              </div>
              <div className='col-md-12 mb-3'>
                <div className='row align-items-center'>
                  <div className='col-md-10 mb-3'>
                    <CFormFloating>
                      <CFormInput
                        type='text'
                        id='value'
                        value={value}
                        onChange={(e) => setValue(e.target.value)}
                        placeholder='مقادیر فیلتر را وارد و بروی Enter بزنید'
                      />
                      <CFormLabel htmlFor='name'>مقادیر</CFormLabel>
                    </CFormFloating>
                  </div>
                  <div className='col-md-2'>
                    <CButton
                      shape='rounded-pill'
                      color='primary'
                      onClick={() => {
                        setValues([...values, { name: value }]);
                        setValue('');
                      }}
                    >
                      <CIcon name={cilPlus} />
                    </CButton>
                  </div>
                  <div className='col-md-12'>
                    {values?.map((el) => {
                      return (
                        <CButton
                          color='warning'
                          shape='rounded-pill'
                          onClick={() => {
                            const arr = [...values];
                            const newArr = values.filter(
                              (e) => e.name !== el.name
                            );
                            setValues(newArr);
                          }}
                        >
                          {el?.name}
                        </CButton>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </CModalBody>
      <CModalFooter>
        <CButton color='secondary' onClick={() => setVisible(false)}>
          انصراف
        </CButton>
        <CButton color='primary' onClick={handleSubmit}>
          ذخیره تغییرات
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default FilterEditModal;
